
.home{
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 10px;
  grid-auto-rows: minmax(100px, auto);
  height: 100%;
}

.searchBar{
    grid-column-start: 1;
    grid-column-end: 8;
    grid-row-start: 1;
    grid-row-end: 1;
}

.navBar{
    grid-column-start: 1;
    grid-column-end: 1;
    grid-row-start: 2;
    grid-row-end: 6;
}

.cards{
    grid-column-start: 2;
    grid-column-end: 8;
    grid-row-start: 2;
    grid-row-end: 6;
    
}